.promoBtnWrapper {
  display: flex;
  column-gap: 34px;
  align-items: center;
}
.withBefore::before {
  content: '';
  display: inline-block;
  width: 190px;
  height: 32px;
  background-image: url(./../../img/icons/decor-yellow.svg);
}
.promoBtn {
  padding: 18px 40px;
  background-color: #000;
  border-radius: 10px;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.5;
  letter-spacing: 1px;
  color: white;
  transition: background-color 0.3s, transform 0.3s ease;
}
.promoBtn:hover{
  background-color: #ebd96b;
  color: black;
  transform: translateY(-2px); 
}
.promoBtn:active{
  background-color: #e4cc2b;
  transform: translateY(0);
}

@media (max-width: 1100px) {
  .promoBtn {
    padding: 15px 30px;
    font-weight: 500;
    font-size: 24px;
  }
  .withBefore::before {
    display: none;
  }
}
@media (max-width: 1000px) {
  .promoBtn {
    padding: 15px 30px;
    font-weight: 500;
    font-size: 24px;
  }
  .promoBtnWrapper {
    column-gap: 10px;
  }
  .withBefore::before {
    display: none;
  }
}
@media (max-width: 876px) {
  .promoBtn {
    padding: 15px 20px;
    font-weight: 500;
    font-size: 20px;
  }
  .withBefore::before {
    display: inline-block;
  }
}
@media (max-width: 500px) {
  .promoBtn {
    padding: 8px 10px;
    font-weight: 400;
    font-size: 20px;
  }
  .withBefore::before {
    display: none;
  }
}
